
import XHeader from 'vux/src/components/x-header/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapGetters } from "vuex";

export default {
directives: {
TransferDom
},
components: {
XHeader,
Previewer,
TransferDom
},

computed: {
...mapGetters(["detailsList"])
},

created() {

},
activated() {
this.$store.dispatch("getrecorddetails", {
id: this.$route.params.detailsid
});
},
deactivated() {
this.details = {};
this.list = [];
},

watch: {
detailsList: function(val) {
this.details = val;
if (val.images) {
val.images.forEach((item, index) => {
this.list.push({ src: item, msrc: val.thumbs[index] });
});
}
}
},
data() {
return {
details: {},

list: [],
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll(".previewer-demo-img")[
index
];

let pageYScroll =
window.pageYOffset || document.documentElement.scrollTop;


let rect = thumbnail.getBoundingClientRect();

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };


}
}
};
},
methods: {
show(index) {
this.$refs.previewer.show(index);
}
}
};



